<template>
	<div id="paybill-list">
		<public-header title="当面付订单列表"></public-header>
		<div class="content">
			<van-list v-model="records.loading" :finished="records.finished" @load="onLoad" :offset="100" :immediate-check="false" v-if="!records.empty" >
				<template v-if="records.data.length > 0">
					<div class="order-item" v-for="(order, orderIndex) in records.data" :key="orderIndex">
						<van-row class="top">
							<van-col span="3">
								<div class="logo" @click="util.jsUrl('/pages/store/goods', {sid: order.sid})">
									<img :src="order.logo" alt="">
								</div>
							</van-col>
							<van-col span="15" class="store">
								<div class="store-title" @click="util.jsUrl('/pages/store/goods', {sid: order.sid})">
									{{order.title}}
									<span class="icon icon-right font-12 c-gray margin-15-l"></span>
								</div>
								<div class="activity" v-if="order.discounts.length > 0">
									<div class="activity-item" v-for="(item, index) in order.discounts" :key="index">{{item.title}}</div>
								</div>
							</van-col>
							<van-col span="6" class="status-cn">已支付</van-col>
						</van-row>
						<div class="padding-15 font-12 van-hairline--top">
							<van-row class="margin-15-b">
								<van-col span="12">消费总额</van-col>
								<van-col span="12" class="text-right">
									<span>{{Lang.dollarSign}}</span><span class="font-14">{{order.total_fee}}</span>
								</van-col>
							</van-row>
							<van-row class="margin-15-b" v-if="order.no_discount_part > 0">
								<van-col span="12">不参与优惠金额</van-col>
								<van-col span="12" class="text-right">
									<span>{{Lang.dollarSign}}</span><span class="font-14">{{order.no_discount_part}}</span>
								</van-col>
							</van-row>
							<van-row class="margin-15-b" v-if="order.discount_fee > 0">
								<van-col span="12">优惠金额</van-col>
								<van-col span="12" class="text-right c-danger">
									-<span>{{Lang.dollarSign}}</span><span class="font-14">{{order.discount_fee}}</span>
								</van-col>
							</van-row>
							<van-divider :dashed="true" />
							<div class="font-14 text-right">
								<span class="c-gray">实际支付金额</span>
								<span class="font-12 c-danger">{{Lang.dollarSign}}</span><span class="c-danger font-bold">{{order.final_fee}}</span>
							</div>
						</div>
						<div class="btn-groups van-hairline--top">
							<van-button size="small" plain color="#ff2d4b" @click="util.jsUrl('/plugin/pages/paybill/detail', {id: order.id})" >详情</van-button>
						</div>
					</div>
					<van-divider style="padding: 0 15px">没有更多了</van-divider>
				</template>
				<van-empty description="暂无订单~" v-else />
			</van-list>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records: {
					page: 1,
					psize: 15,
					loading: true,
					finished: false,
					empty: false,
					data:[]
				},
				showPreLoading: true,
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getRecords({
					vue: that,
					url: 'wmall/order/paybill/list',
					data: {},
					success: (res) => {

					},
				})
			},
		},
		mounted () {
			this.onLoad()
		},
	}
</script>

<style>
	#paybill-list .order-item{
		margin: 10px;
		background-color: #fff;
		border-radius: 5px;
	}
	#paybill-list .order-item .top{
		padding: 15px;
		display: flex;
		align-items: center;
	}
	#paybill-list .order-item .logo{
		position: relative;
		width: 90%;
		padding-top: 90%;
	}
	#paybill-list .order-item .logo img{
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 3px;
	}
	#paybill-list .order-item .store{
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: center;
		padding-left: 5px;
		font-weight: 500;
	}
	#paybill-list .order-item .store-title{
		width: 100%;
		display: flex;
		align-item: center;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 14px;
	}
	#paybill-list .order-item .activity{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		border-radius: 3px;
		padding: 3px 3px;
		font-size: 12px;
		color: rgba(255, 45, 75, .8);
		border: 1px solid  rgba(255, 45, 75, .8);
		transform: scale(.9);
		margin-top: 5px;
		margin-left: -5px;
	}
	#paybill-list .order-item .activity .activity-item{
		padding: 0 3px;
		border-right: 1px solid rgba(255, 45, 75, .8);
	}
	#paybill-list .order-item .activity .activity-item:last-child{
		border: 0px;
	}
	#paybill-list .order-item .status-cn{
		color: #7a7a7a;
		font-size: 14px;
		text-align: right;
	}


	#paybill-list .order-item .btn-groups{
		display: flex;
		align-items: center;
		justify-content: flex-end;
		padding: 15px;
	}
	#paybill-list .order-item .btn-groups .van-button{
		margin-left: 10px;
		border-radius: 5px;
	}
</style>


